					// 		href2: "https://www.o-fldn.info/gold#reg-form"
					// 	},
					// ],
				],
				benefits_Messages: [{
					src: require("../assets/img/icon-anm-coins.svg"),
					alt: "外汇",
					title_name: "外汇",
					list_name_one: "全球主要货币对",
					list_name_two: "点差低至",
					list_name_three: "杠杆高达2000倍",

				}, {
					src: require("../assets/img/icon-anm-gold.svg"),
					alt: "黄金、白银",
					title_name: "黄金、白银",
					list_name_one: "行业领先点差低至0",
					list_name_two: "杠杆高达200倍",
					list_name_three: "隔夜费低至0.7美元",

				}, {
					src: require("../assets/img/icon-anm-graph.svg"),
					alt: "股票CFD",
					title_name: "股票CFD",
					list_name_one: "美股港股齐备",
					list_name_two: "美股有苹果、特斯拉等",
					list_name_three: "港股有腾讯、比亚迪等",

				}, {
					src: require("../assets/img/icon-anm-oil.svg"),
					alt: "商品和指数CFD",
					title_name: "商品和指数CFD",
					list_name_one: "A50点差低至1",
					list_name_two: "美油布油杠杆高达200倍",
					list_name_three: "标普道指杠杆高达500倍",

				}, {
					src: require("../assets/img/icon-anm-pips.svg"),
					alt: "股票交易",
					title_name: "股票交易",
					list_name_one: "美股享有零佣金",
					list_name_two: "美股港股提供碎股交易",
					list_name_three: "美股港股提供碎股交易",

				}],
				steps_boxs_items: [{
					src: require("../assets/img/step1.svg"),
					content: "开设MyFXTM账户"
				}, {
					src: require("../assets/img/step2.svg"),
					content: "选择并开设交易账户"
				}, {
					src: require("../assets/img/step3.svg"),
					content: "完成首笔入金"
				}, {
					src: require("../assets/img/step4.svg"),
					content: "登录MT4或MT5交易平台"
				}, {
					src: require("../assets/img/step5.svg"),
					content: "开始非凡交易之旅！"
				}],
				myswiper: '',
				i:0,
				benefits_view: false,
				DropdownBtnStatus:false,
				screenWidth: '',
				steps_view: false,
				riskClosedBtn: false,
				DropdownItemBtnStatus: false,	
				DropdownItemBtnStatus2: false,
				DropdownItemBtnStatus4: false,	
				DropdownItemBtnStatus5: false,
				DropdownItemBtnStatus3: false,
				DropdownItemBtnStatus6: false,
				showcdrpstatus1:false,								
			};
		},
		mounted() {
			let _this = this;
			window.addEventListener("scroll", this.handleScroll); // 监听滑动事件
			this.$nextTick(function() {
				_this.initData();
			});
			// window.addEventListener('scroll', this.handleScroll, true);  //还要重新命名一下
			// this.screenWidth = document.body.clientWidth
			// window.onresize = () => { 
			// 	return (() => {
			// 		this.screenWidth = document.body.clientWidth
			// 	})()
			// }
		},
		watch: {
			screenWidth: {
				handler: function (val, oldVal) {

					if (val < 991) {
						this.benefits_view = true
						this.steps_view = true
						this.showsmallStatus = false;
					} else {
						this.benefits_view = false
						this.steps_view = false
						this.showsmallStatus = true;
					}
					
					immediate: true
				}
			}
		},
		methods: {
			handleScroll() {
				var scrollTop =
					window.pageYOffset ||
					document.documentElement.scrollTop ||
					document.body.scrollTop;
				// console.log(scrollTop) // 滑动的长度
				var offsetTop = document.querySelector(".header_navbar").offsetHeight;
				var scroll = scrollTop - this.i;
				this.i = scrollTop;
				if(scroll<0){
					this.isHeaderFixed = false;
				}else{
					this.isHeaderFixed = true;
				}
				// 吸顶效果
				if (scrollTop > offsetTop) {
					this.isFixed = true;
				} else {
					this.isFixed = false;
					this.isHeaderFixed = false;
				}
			},
			toTop() {
				document.documentElement.scrollTop = 0;
			},
			//搜索跳转
			SearchTo() {
				console.log(this.searchValue)
				// window.location.href = 'https://www.futuo-fx.net/search?text=' + this.searchValue
			},
			//风险交易关闭
			riskClosed() {
				this.riskClosedBtn = true;
			},
			//最新优惠
			newpreFerential(val) {

				if (val == 1) {
					this.isShow = !this.isShow;
					this.isShowImg = false;
				} else {
					this.isShowImg = !this.isShowImg;
					this.isShow = false;
				}
			},
			Todrps(val) {
				this.DropdownItemBtnStatus = false;
				this.DropdownItemBtnStatus2 = false;
				this.DropdownItemBtnStatus3 = false;
				this.DropdownItemBtnStatus4 = false;
				this.DropdownItemBtnStatus5 = false;
				this.DropdownItemBtnStatus6 = false;
				this.mainDropdownBtnStatus2 = true;
				
				if (val == 1) {
				
					this.DropdownItemBtnStatus = true;
				} else if (val == 2) {
				
					this.DropdownItemBtnStatus2 = true;
				} else if (val == 3) {
				
				this.DropdownItemBtnStatus3 = true;
				} 	else if (val == 4) {
					this.DropdownItemBtnStatus4 = true;
				}else if (val == 5) {
					this.DropdownItemBtnStatus5 = true;
				}
				else if (val == 6) {
					this.DropdownItemBtnStatus6 = true;
				}
				
				if (val == '返回') {
					this.mainDropdownBtnStatus2 = false;
					this.DropdownItemBtnStatus = false;
					this.DropdownItemBtnStatus2 = false;
					this.DropdownItemBtnStatus3 = false;
					this.DropdownItemBtnStatus4 = false;
					this.DropdownItemBtnStatus5 = false;
					this.DropdownItemBtnStatus6 = false;
				}

			},
			//showSurvey
			showSurvey() {
				this.isShowSuvery = !this.isShowSuvery;
			},
			//展示下拉框
			ShowDropdown(val) {
				this.DropdownItemBtnStatus = false;
				this.DropdownItemBtnStatus2 = false;
				this.DropdownItemBtnStatus3 = false;
				this.DropdownItemBtnStatus4 = false;
				this.DropdownItemBtnStatus5 = false;
				this.DropdownItemBtnStatus6 = false;
				if (val == 'hide') {
					this.DropdownBtnStatus = false;
					this.mainDropdownBtnStatus = false;
					// this.mainDropdownBtnStatus2 = true;
				} else {
					this.DropdownBtnStatus = true;
					this.mainDropdownBtnStatus = true;
					this.mainDropdownBtnStatus2 = false;
				}
				
			},
			showsmall() {
				this.showsmallStatus = !this.showsmallStatus;
			},
			cdrp() {
				this.showcdrpstatus1 = true
				// this.showcdrpstatus2 = false
			},
			initData() {
				let _this = this
				
				this.screenWidth = document.body.clientWidth
				window.onresize = () => {
					this.screenWidth = document.body.clientWidth
					// // alert(document.body.clientWidth)
					// if (this.screenWidth < 1200) {
					// 	this.benefits_view = true
					// 	this.steps_view = true
					// }
					// return (() => {
					// 	this.screenWidth = document.body.clientWidth
					// })
				}


				_this.myswiper = new swiper('#swiper1', {
					slidesPerView: 1,

					loop: true,
					autoplay: true,
					loopFillGroupWithBlank: true,
					observer: true, //修改swiper自己或子元素时，自动初始化swiper 
					observeParents: true, //修改swiper的父元素时，自动初始化swiper 
					onSlideChangeEnd: function(swiper) {
						swiper.update();
						_this.myswiper.startAutoplay();
						_this.myswiper.reLoop();

					},

				});

				var myswiper2 = new swiper('#swiper2', {
					slidesPerView: 1,

					autoplay: true,
					loop: true,

					onSlideChangeEnd: function(swiper) {
						swiper.update();
						myswiper2.startAutoplay();
						myswiper2.reLoop();
					},
					pagination: {
						el: '.swiper-pagination',
						clickable: true,
					},

				});
				var myswiper3 = new swiper('#swiper3', {
					// slidesPerView: 1,

					autoplay: true,
					loop: true,

					// onSlideChangeEnd: function(swiper) {
					// 	swiper.update();
					// 	myswiper3.startAutoplay();
					// 	myswiper3.reLoop();
					// },
					pagination: {
						el: '#swiper3 .swiper-pagination',
						// clickable: true,

						renderBullet: function(index, className) {
							if (index < _this.steps_boxs_items.length - 1) {
								return '<span class="' + className + '">' + '</span>' +
									"<svg width='30' height='2'>"

									+
									"<line  fill='yellow' y1='1' x2='30' y2='1' stroke='#d5d5d8' stroke-width='2' stroke-dasharray='4 4'/>" +
									"</svg>"
							} else {
								return '<span class="' + className + '">' + '</span>'
							};
						},

					},
				});
				// for (i = 0; i < myswiper3.pagination.bullets.length; i++) {
				// 	myswiper3.pagination.bullets[i].onmouseover = function() {
				// 		this.click();
				// 	};
				// }
				//swiper3
				_this.myswiper.el.onmouseover = function() {
					_this.myswiper.autoplay.stop();
				}
				_this.myswiper.el.onmouseout = function() {
					_this.myswiper.autoplay.start();
				}
			}
		}
	};
</script>
