	//navbar
	.header_navbar {
		box-sizing: border-box;
		background-color: #fff;
		height: 73px;
		width: 100%;

		
		&.fixed {
			position: fixed;
			top: 0;
			background-color: rgba(26, 29, 48, 0.76);
			border: 1px solid rgba(26, 29, 48, 0.76);
			box-sizing: border-box;
			transition: all 0.5s;
			z-index: 10000;

			.header_nav-link {
				color: #fff ;
			}

			.header_nav-link {
				height: 53px;
			}

			.header_logo_white_img {
				display: block !important;
			}

			.header_logo_img {
				display: none !important;
			}
		}

		&.imgfixed {
			display: block !important;
		}

		.header_navbar-nav_item {
			position: relative;

			// &:hover {
			// 	.header_nav-link {
			// 		color: #ff4521;
			// 	}

			// 	.flat-menuf {
			// 		display: block !important;
			// 	}

			// 	.flat-menu1 {
			// 		display: flex !important;
			// 	}
			// }

			.flat-menuf {
				display: none;

				.flat-menu {
					width: 426px !important;
					height: 275px;
					padding: 16px 8px;
					align-items: center;
					width: auto;
					left: 50%;
					transform: translate(-50%);
					background-color: #1a1d30;
					position: absolute;
					display: flex;
					justify-content: space-around;
					color: #fff !important;

					.flat-menu_lfet {
						display: flex;
						align-items: center;
						height: 100%;
						width: 185px;

						ul {
							width: 185px;

							li {
								display: block;

								a {
									color: white;
									padding: 6px 16px;
									display: block;
									border-radius: 8px;
									font-weight: bold;

									&:hover {
										color: white;
										text-decoration: none;
										background: rgba(255, 255, 255, 0.141176);
									}
								}
							}
						}
					}

					.flat-menu_right {
						border-left: 1px solid #fff;
						padding-left: 24px;

						.green_btn {
							height: 42px;
							margin-top: 30px;

							a {
								padding: 10px 18px;
								background-color: #a4e22d !important;
								border: 1px solid #a4e22d !important;
								color: #1a1d30 !important;
								transition: 0.2s;
								padding-left: 33px;
								padding-right: 33px;
								font-weight: 700;
								border-radius: 32px;
								
								text-decoration: none;
								text-align: center;

								&:hover {
									transition: 0.2s;
									background-color: transparent !important;
									color: #a4e22d !important;
								}
							}
						}

						.mega-menu_promo-block_title {
							font-size: 20px;
							line-height: 28px;
							margin-bottom: 8px;
						}

						.mega-menu_promo-block_subtitle {
							p {
								margin-bottom: 24px;
								font-size: 12px;
								line-height: 18px;
							}
						}
					}
				}
			}
		}

		.header_nav-link {
			height: 54px;
			line-height: 48px;
			
			color: #1a1d30;
			font-weight: 700;
			font-size: 18px;
			display: inline-block;
			padding-left: 36px;
			// padding-right: 20px;
			border-bottom: 4px solid transparent;
			// 
		}

		.header_navbar-brand-wrapper {
			padding: 15px 80px;

			.header_logo {
				margin-right: 45px;

				.header_logo_img {
					width: 253px;
