				}

				.header_logo_white_img {
					width: 253px;
					height: 46.61px;
					display: none;
				}
			}

			display: flex;
			justify-content: flex-start;
			// background-color: rgba(26, 29, 48, 0.76);
			// border: 1px solid rgba(26, 29, 48, 0.76);

			box-sizing: border-box;
